<template>
  <div>
    <van-tabbar
      style="z-index:300;font-size:.11rem"
      v-model="active"
      active-color="#000"
      inactive-color="#000"
    >
      <van-tabbar-item
        @click="onChange(index)"
        v-for="(item,index) of tabbarArr"
        :key="index"
        :to="item.url"
      >
        <span>{{item.txt}}</span>
        <template #icon="props">
          <img :src="props.active ? item.img2 : item.img1" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tabbarArr: [
        {
          txt: "首页",
          img1: require("./../assets/images/icon_home_normal@2x.png"),
          img2: require("./../assets/images/icon_home_select@2x.png"),
          url: "/"
        },
        {
          txt: "消息",
          img1: require("./../assets/images/icon_message_normal@2x.png"),
          img2: require("./../assets/images/icon_message_select@2x.png"),
          url: "/message"
        },
        {
          txt: "发现",
          img1: require("./../assets/images/icon_discory_normal@2x.png"),
          img2: require("./../assets/images/icon_discory_select@2x.png"),
          url: "/discover"
        },
        {
          txt: "我的",
          img1: require("./../assets/images/icon_mine_normal@2x.png"),
          img2: require("./../assets/images/icon_mine_select@2x.png"),
          // url: "/"
        }
      ],
      active: JSON.parse(window.localStorage.getItem("keyIndex")) || 0
    };
  },
  // mounted() {
  //   window.localStorage.removeItem("keyIndex");
  // },
  methods: {
    onChange(index) {
      window.localStorage.setItem("keyIndex", JSON.stringify(index));
    }
  }
};
</script>
<style lang="less" scoped>
/deep/ .van-tabbar-item__icon img {
  width: 0.28rem;
  height: 0.28rem;
}
</style>